.skill-heading {
  color: white;
  background-color: firebrick;
  letter-spacing: 3px;
  margin: 30px auto 4px;
  padding: 6px 8px 4px;
  cursor: pointer;
  display: block;
  box-shadow: 0 0 1px grey;
  border-radius: 4px;
  font-size: 1.2em;
  font-weight: 800;
  text-transform: uppercase;
}