#nav-bar {
  width: 100%;
  max-width: 1000px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0px 2px rgba(0, 0, 0, 0.6);
}